<template>
  <app-layout>
    <div class="inner-contents">
      <h2 class="contents-title">  </h2>
      <p class="contents-text">   .</p>
      <div class="collapse-container">
        <faq-collapse :title="studentTitle" :items="studentItems"></faq-collapse>
        <faq-collapse :title="instructorTitle" :items="instructorItems"></faq-collapse>
      </div>
    </div>
  </app-layout>
</template>

<script>
import AppLayout from '@/components/layout/AppLayout'
import FaqCollapse from '@/components/faq/FaqCollapse'

export default {
  name: 'Faq',
  data () {
    return {
      studentTitle: '?',
      studentItems: [
        { title: '.', text: '.' },
        { title: '.', text: '.' }
      ],
      instructorTitle: '?',
      instructorItems: [
        { title: '.', text: '.' },
        { title: '.', text: '.' }
      ]
    }
  },
  components: {
    AppLayout,
    FaqCollapse
  }
}
</script>

<style lang="scss" scoped>
.collapse-container {
  padding-bottom: map-get($spacers, 5);

  @include clearfix();
}
</style>



