<template>
  <div class="form form-signin" v-loading="loading">
    <el-card shadow="never">
      <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')">
        <el-form-item label="" prop="email">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="form.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button native-type="submit" plain class="btn-block"></el-button>
        </el-form-item>
        <div class="link-box">
          <router-link :to="'/'" class="link"> ?</router-link>
          <router-link :to="'/signup'" class="link">  ?</router-link>
        </div>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'SignInCommon',
  data () {
    return {
      loading: false,
      passwordVisible: false,
      form: {
        email: '',
        password: ''
      },
      rules: {
        email: [
          { required: true, message: ' .', trigger: 'blur' },
          { type: 'email', message: '  .', trigger: 'blur' }
        ],
        password: [
          { required: true, message: ' .', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          //  
          this.loading = true
          //   
          const formData = {
            email: this.form.email,
            password: this.form.password,
            lastLoginedAt: new Date().getTime()
          }
          //  boolean
          const bool = await this.$store.dispatch('signIn', formData)
          //   
          if (bool) {
            this.$message({
              showClose: true,
              message: ' .',
              type: 'success'
            })
            this.$router.replace(this.$route.query.redirect || '/')
          }
          //  
          this.loading = false
        } else {
          return false
        }
      })
    },
    async openFindPassword () {
      try {
        const email = await this.$prompt('', ' ', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
          inputErrorMessage: '  .'
        })
        const bool = await this.$store.dispatch('passwordResetEmail', email)
        if (bool) {
          this.$message({
            message: '  .',
            type: 'success'
          })
        }
      } catch (err) {
        // todo
      }
    }
  }
}
</script>

<style lang="scss" scoped>

</style>



