<template>
  <el-card shadow="never" class="mypage-card">
    <div class="thumb">
      <img :src="info.profile.downloadURL" alt="thumb">
    </div>
    <div class="info">
      <strong class="title">{{ info.name }}</strong><br>
      <span class="email">: {{ info.email }}</span><br>
      <span class="phone">: {{ info.phone }}</span><br>
      <router-link :to="'/changeinfo'" class="link"></router-link>
    </div>
  </el-card>
</template>

<script>
export default {
  name: 'MyInfo',
  props: {
    info: {
      type: Object,
      required: true
    }
  }
}
</script>



